const Base = () => import('@/components/moderators/Base');
const Barbershops = () => import('@/pages/moderators/Barbershops');
const Barbers = () => import('@/pages/moderators/Barbers');
const Login = () => import('@/pages/moderators/Login');
const Users = () => import('@/pages/moderators/Users');

const admin = [
  {
    path: '/moderadores/login',
    component: Login,
    name: 'moderators-login'
  },
  {
    path: '/moderadores',
    component: Base,
    children: [
      {
        path: '',
        name: 'moderators',
        redirect: { name: 'moderators-barbershops' }
      },
      {
        path: 'barbearias',
        component: Barbershops,
        name: 'moderators-barbershops',
        meta: {
          pageName: 'Barbearias',
          alias: 'barbershops',
          css: 'pa-0',
          isModerator: true,
          requiresAuth: true,
          breadcrumbs: [
            { text: "Dashboard", disabled: false, href: "/admin" },
            { text: "Agenda", disabled: true }
          ]
        }
      },
      {
        path: 'barbeiros',
        component: Barbers,
        name: 'moderators-barbers',
        meta: {
          pageName: 'Barbeiros',
          alias: 'barbers',
          css: 'pa-0',
          isModerator: true,
          requiresAuth: true,
          breadcrumbs: [
            { text: "Dashboard", disabled: false, href: "/moderators" },
            { text: "Barbeiros", disabled: true }
          ]
        }
      },
      {
        path: 'usuarios',
        component: Users,
        name: 'moderators-users',
        meta: {
          pageName: 'Usuários',
          alias: 'users',
          isModerator: true,
          requiresAuth: true,
          breadcrumbs: [
            { text: "Dashboard", disabled: false, href: "/admin" },
            { text: "Usuários", disabled: true }
          ]
        }
      }
    ]
  }
]

export default admin