const Barbershop = () => import('@/pages/site/Barbershop');
const Base = () => import('@/pages/site/Base');
const ChangePassword = () => import('@/pages/site/ChangePassword');
const HomeSite = () => import('@/pages/site/Home');
const Offline = () => import('@/pages/site/Offline');
const PrivacyPolicy = () => import('@/pages/site/PrivacyPolicy');
const Profile = () => import('@/pages/site/Profile');
const Schedule = () => import('@/pages/site/Schedule');
const ScheduleDetail = () => import('@/pages/site/ScheduleDetail');
const Terms = () => import('@/pages/site/Terms');

const site = [
  {
    path: '',
    component: Base,
    children: [
      {
        path: '/',
        component: HomeSite,
        name: 'home',
        meta: { isAdmin: false },
      },
      {
        path: 'alterar-senha',
        name: 'site-change-password',
        component: ChangePassword,
        meta: {
          isAdmin: false,
          fluid: true,
          noPadding: true,
          backButton: true,
          backButtonRouteName: 'home',
        },
      },
      {
        path: 'barbearia/:id/:url',
        name: 'site-barbershop',
        component: Barbershop,
        meta: {
          isAdmin: false,
          fluid: true,
          noPadding: true,
          backButton: true,
          backButtonRouteName: 'home',
        },
      },
      {
        path: 'meus-agendamentos',
        component: Schedule,
        name: 'site-schedule',
        meta: {
          requiresAuth: true,
          isAdmin: false,
          menuButton: true,
          alias: 'schedule',
          pageTitle: 'Meus agendamentos',
        },
      },
      {
        path: 'agendamento/:id',
        component: ScheduleDetail,
        name: 'site-schedule-detail',
        meta: {
          requiresAuth: true,
          isAdmin: false,
          backButton: true,
          alias: 'schedule',
          pageTitle: 'Agendamento',
        },
      },
      {
        path: 'offline',
        name: 'offline',
        component: Offline,
        meta: { isAdmin: false },
      },
      {
        path: '/politica-de-privacidade',
        name: 'site-privacy-policy',
        component: PrivacyPolicy,
        meta: {
          backButton: true,
          backButtonRouteName: 'home',
          isAdmin: false,
          pageTitle: 'Política de Privacidade',
          pageCssClass: 'terms',
        },
      },
      {
        path: '/meu-perfil',
        name: 'site-profile',
        component: Profile,
        meta: {
          backButton: true,
          fluid: true,
          noPadding: true,
          backButtonRouteName: 'home',
          isAdmin: false,
          pageTitle: 'Meu Perfil',
          pageCssClass: 'profile',
        },
      },
      {
        path: '/termo-de-uso',
        name: 'site-terms',
        component: Terms,
        meta: {
          backButton: true,
          backButtonRouteName: 'home',
          isAdmin: false,
          pageTitle: 'Termo de Uso',
          pageCssClass: 'terms',
        },
      },
    ],
  },
];

export default site;
