import axios from 'axios';

const Axios = axios.create({
  // baseURL: 'http://localhost:8000/api',
  baseURL: 'https://api.appmeubarbeiro.com.br/api',
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: false,
});

Axios.interceptors.request.use(
  function (config) {
    const token = localStorage.token;
    const userToken = localStorage.userToken;
    if (token) config.headers.Token = token;
    else if (userToken) config.headers.Token = userToken;
    config.headers.moderator_token = localStorage.moderatorToken || null
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

Axios.interceptors.response.use(
  function (response) {
    return response;
  },
  function (error) {
    if (401 === error.response.status) {
      window.console.error(error.response.data)
      let path = window.location.pathname;
      path = path.replace('/', '');
      localStorage.routePath = path;
      let destiny = '/autenticacao/login'
      if (path.includes('admin')) destiny = '/autenticacao/barbeiro/login'
      else if (path.includes('moderadores')) destiny = '/moderadores/login'
      window.location = destiny;
    } else return Promise.reject(error);
  }
);

export default Axios;
