const state = {
  aceptedTerm: localStorage.aceptedTerm || null,
};

const getters = {
  aceptedTerm: (state) => {
    return state.aceptedTerm;
  },
};

const actions = {
  setAceptedTerm({ commit }, aceptedTerm) {
    localStorage.aceptedTerm = aceptedTerm;
    commit('setAceptedTerm', aceptedTerm);
  },
};

const mutations = {
  setAceptedTerm(state, aceptedTerm) {
    state.aceptedTerm = aceptedTerm;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
